module.exports = ['AuthFactory','$scope','$state','$rootScope',function(AuthFactory,$scope,$state,$rootScope) {
  var that = this;
  this.links= [];
  
  $scope.$watch(function(){
    return AuthFactory.isAuthorized();
  }, function (newValue) {
    that.links = makeLinks(newValue);
  });
  
  function makeLinks(isLoged) {
    if(isLoged){
      return [
        {
          title: '',
          link: 'group.news'
        },{
          title: '',
          link: 'cabinet'
        },{
          title: '',
          link: 'logout'
        }
      ];
    }else{
      return [
        {
          title: '',
          link: 'contacts'
        },{
          title: '',
          link: 'login'
        },{
          title: '',
          link: 'register'
        }
      ];
    }
  }
  function getSubLinks() {
    if($state.includes('group')){
      return [
        {
          title: '',
          link: 'group.news'
        }, {
          title: '',
          link: 'group.users'
        }, {
          title: '',
          link: 'group.files'
        }
      ];
    } else{
      return [];
    }
  }

  $rootScope.$on('$stateChangeSuccess',function() {
    $scope.subLinks = getSubLinks();
  });
  
  that.links = makeLinks(AuthFactory.isAuthorized());
}];
